import React, { Component } from 'react';
import { FaReact } from "react-icons/fa";
import {
  DiJavascript1,
  DiReact,
  DiNodejs,
  DiGit,
} from "react-icons/di";
import { FaAngular, FaHtml5,FaCss3   } from "react-icons/fa";
import { IoLogoIonic } from "react-icons/io";
import { TbBrandVscode } from "react-icons/tb";
import { SiPostman } from "react-icons/si";
import { FaWindows } from "react-icons/fa";
import { FaFileCode } from "react-icons/fa6";

export default class Porfolio extends Component {
  render() {
    let resumeData = this.props.resumeData;
    return (
      <section id="portfolio">

            <h1>Professional 
              <strong className='purple'> Skillset</strong></h1> 
      <div className="tech-icons-container">  
          <h2 className="tech-icons">  <DiReact /> </h2>  
          <h2 className="tech-icons">  <DiJavascript1 /> </h2>
          <h2 className="tech-icons">  <DiNodejs /> </h2>
         <h2 className="tech-icons">  <IoLogoIonic /> </h2>
         <h2 className="tech-icons">  <FaAngular /> </h2>

        </div>
        <h1> <strong className='purple'>  Tools </strong> i Use</h1> 
      <div className="tech-icons-container">  
          <h2 className="tech-icons">  <TbBrandVscode /> </h2>  
          <h2 className="tech-icons">  <SiPostman /> </h2>
          <h2 className="tech-icons">  <FaWindows /> </h2>
         <h2 className="tech-icons">  <FaFileCode /> </h2>

        </div>
      <div className="row">
        <div className="twelve columns collapsed">
          <h1>Check Out Some of <strong className='purple'>  My Works.</strong></h1>
          <div id="portfolio-wrapper" className="bgrid-quarters s-bgrid-thirds cf">
          {
            resumeData.portfolio && resumeData.portfolio.map((item)=>{
              return(
                <div className="columns portfolio-item">
                  <div className="item-wrap">
                    <a href="#modal-01">
                      <img src={`${item.imgurl}`} className="item-img"/>
                      <div className="overlay">
                        <div className="portfolio-item-meta">
                          <h5>{item.name}</h5>
                          <p>{item.description}</p>
                        </div>
                      </div>
                    </a>
                  </div>
                </div>
              )
            })
          }
          </div>
        </div>
        
      </div>
    
  </section>
        );
  }
}